            <li><code class="token">$root-font-size</code>를 기준으로 px단위는 변경됩니다. rem기준으로 정의합니다.</li>
            <li>소수점은 반올림 시켜 정수로만 정의합니다.</li>
          </ul>
        </div>

        <pre class="code-block">
$font-size-root:  16px;

$font-size-display-lg: round($font-size-root * 2.5);
$font-size-display-md: round($font-size-root * 2.2);
$font-size-display-sm: round($font-size-root * 2);

$font-size-heading-lg: round($font-size-root * 1.6);
$font-size-heading-md: round($font-size-root * 1.4);
$font-size-heading-sm: round($font-size-root * 1.2);

$font-size-body-lg: $font-size-root + 1;
$font-size-body-md: $font-size-root;
$font-size-body-sm: $font-size-root - 1;

$font-size-caption-lg: $font-size-root - 2;
$font-size-caption-md: $font-size-root - 3;
$font-size-caption-sm: $font-size-root - 4;

$line-height-display-lg: round($font-size-display-lg * 1.3);
$line-height-display-md: round($font-size-display-md * 1.3);
$line-height-display-sm: round($font-size-display-sm * 1.3);

$line-height-heading-lg: round($font-size-heading-lg * 1.3);
$line-height-heading-md: round($font-size-heading-md * 1.3);
$line-height-heading-sm: round($font-size-heading-sm * 1.3);

$line-height-body-lg: round($font-size-body-lg * 1.6);
$line-height-body-md: round($font-size-body-md * 1.6);
$line-height-body-sm: round($font-size-body-sm * 1.6);

$line-height-caption-lg: round($font-size-caption-lg * 1.5);
$line-height-caption-md: round($font-size-caption-md * 1.5);
$line-height-caption-sm: round($font-size-caption-sm * 1.5);

--font-weight-black: 900;
--font-weight-extrabold: 800;
--font-weight-bold: 700;
--font-weight-semibold: 600;
--font-weight-medium: 500;
--font-weight-regular: 400;
--font-weight-light: 300;
