import Vue from 'vue';
import VueRouter from 'vue-router';
// import MainView from '../views/MainView.vue';
import baseColor from '../views/foundation/ColorView.vue';
import baseTypo from '../views/foundation/TypographyView.vue';
import baseButton from '../views/component/ButtonView.vue';

Vue.use(VueRouter);

const routes = [
  // {
  //   path: '/',
  //   name: 'MainView',
  //   component: MainView,
  // },
  {
    path: '/color',
    name: 'baseColor',
    component: baseColor,
  },
  {
    path: '/',
    name: 'baseTypo',
    component: baseTypo,
  },
  {
    path: '/button',
    name: 'baseButton',
    component: baseButton,
  },
];

const router = new VueRouter({
  mode: 'hash',
  routes,
});

export default router;